<template>
  <div class="gpc-select">
    <gpc-select :value="selectedObj" @input="selected = $event && $event.BrickCode"></gpc-select>
    <input type="hidden" v-model="selected" name="category"/>
  </div>
</template>

<script>
  import GpcSelect from './../../src/components/Common/Form/GpcSelect/GpcSelect'

  export default {
    name: 'GpcSelectDemo',
    components: {
      GpcSelect
    },
    data() {
      return {
        selectedObj: null,
        selected: null
      }
    },
    created() {
      this.selected = this.$parent.$el.value
      if (this.selected && this.selected.length > 0) {
        this.selectedObj = {BrickCode: this.selected}
      }
    }

  }
</script>

