let _token
export default {
  get _token() {
    return _token
  },
  set _token(val) {
    _token = val
  },
  init() {
    this._token = undefined
    const tokenEl = document.getElementById('auth_token')
    if (tokenEl) {
      this._token = tokenEl.innerHTML
    }
  },
  redirect(url) {
    window.location.assign(url)
  },
  loginUrl: '/login',
  getToken() {
    return this._token
  },
  redirectToLogin() {
    this.redirect(this.loginUrl)
  }
}
